import "./styles.css"
import Nanny from "nanny-state"
import { shuffle } from "jog-list"
import QUESTIONS from "./questions.js"

const vroom = new Audio("../sounds/vroom.wav")
const screech = new Audio("../sounds/screech.wav")
const horn = new Audio("../sounds/horn.wav")

const View = state => {
  // event handle
  const submitAnswer = answer => event => {
    console.log(state.questions.length)
    const correct = answer === state.questions[state.questionNumber].answers[0]
    const index = QUESTIONS.findIndex(x => x.question === state.questions[state.questionNumber].question)
    if (correct) {
      vroom.play()
      setTimeout(_ => {
        vroom.pause()
        vroom.currentTime = 0
      }, 1950)
    } else {
      screech.play()
      setTimeout(_ => {
        screech.pause()
        screech.currentTime = 0
      }, 1000)
    }
    state.Update({
      score: correct ? state.score + 1 : state.score,
      fredback: correct ? "Correct!" : "Wrong!",
      questionNumber: state.questionNumber + 1,
      mistakes: correct ? state.mistakes.filter((_,i) => i !== index) : [...state.mistakes,index]
    })
  }

  const playAgain = mistakes => event => {
    horn.play()
    state.Update({...State,questions: mistakes ? shuffle(QUESTIONS.filter((_,i) => state.mistakes.includes(i))): shuffle(QUESTIONS)});
  }
  const result = (score,total) => 
    score/total < .2
      ? "Oh dear, you'd probably manage to crash in a stationary vehicle"
      : score/total < .6
      ? "Not great, you should stick to Mario Kart"
      : score/total < 1
      ? "Not bad, you probably won't smush into anyone at least"
      : "Yeehaw! You're practically an F1 driver!"
  // View code
  return state.HTML`
<h1><span class="JOG"><span class="J">J</span>O<span class="G">G</span></span> Driving Schoo<span id="L-plate">l</span></h1>
<div id="game">
<h2 id="score">Score: ${state.score}</h2>
${
  state.questionNumber < state.questions.length && state.questionNumber < 10
    ? state.HTML`<p id="question">${state.questionNumber + 1}) ${
        state.questions[state.questionNumber].question
      }${state.questions[state.questionNumber].image ? state.HTML`<img src=${`../images/${state.questions[state.questionNumber].image}`} alt="road sign">` : ""}</p>
<div id="answers">
${shuffle(state.questions[state.questionNumber].answers).map(
  (answer) =>
    state.HTML`<button onclick=${submitAnswer(answer)}>${answer}</button>`
)}   
</div>
<h2 id="fredback">${state.fredback}</h2>
`
    : state.HTML`<div id="game-over">
      <h2>${result(state.score,state.questionNumber)}</h2>
      <h2>Why don't you have another go?</h2>
    </div>
<button onclick=${playAgain(false)}>All Questions</button>
${state.mistakes.length ? state.HTML`<button onclick=${playAgain(true)}>Practice Your Mistakes</button>`:""}`
}
<div id="car" style=${`left: ${state.screenSize/10 * state.score}px`}></div>
</div>
<footer>
  <p>Another beatifully unconventional <span class="JOG"><span class="J">J</span>O<span class="G">G</span></span> production made with ðŸ’œ in <a href="https://github.com/daz4126/Nanny-State" title="Nanny State">Nanny State</a>.</p>
  <p><small><a href="https://www.flaticon.com/free-icons/car" title="car icons">Car icons created by DinosoftLabs - Flaticon</a></small></p>
  </footer>`
}

const State = {
  questions: shuffle(QUESTIONS),
  score: 0,
  questionNumber: 0,
  mistakes: [],
  screenSize: window.innerWidth,
  fredback: "Hope this helps you smush your theory OG! xx",
  View
}
// start the Nanny State!
Nanny(State)
